<template>
	<div class="orderList">
		<div class="order-item" v-for="(order,index) in orderList">
			<div class="orderId clearfix">
				<div class="orderSn fl">
					订单编号: {{order.order_sn}}
				</div>
				<div class="orderStatus fr">
					{{groupStatus(order.status)}}
				</div>
			</div>
			<div class="order-content">
				<div class="content-text">
					<div class="title" v-text="order.plan_name"></div>
					<div class="msg">投保人: {{order.companyName}}</div>
					<div class="msg">被保险人: {{name}}</div>
					<div class="msg">保障期间: {{order.policy_effective_date}} 至 {{order.policy_expiration_date}}</div>
				</div>
				<div class="logo"><img :src='baseUrl+order.brandLogo'></div>
				<div class="btn-contair">
					<!-- <div class="toSettlement" v-if="order.status==3">申请理赔</div> -->
					<div class="toDetail" @click='handleClickToDetail(order)'>查看详情</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  props: {
    orderList: {
      type: Array,
      required: true
    }
  },
  methods: {
    handleClickToDetail (order) {
      this.$router.push('/37duc/personCenter/groupDetail/' + order.id)
    },
    groupStatus (status) {
      let res
      switch (status) {
        case 0: res = '未完成'; break
        case 1: res = '待支付'; break
        case 2: res = '已出单'; break
        case 3: res = '保障中'; break
        case 4: res = '已失效'; break
        case 5: res = '已退保'; break
        case 6: res = '已取消'; break
      }
      return res
    }
  },
  data () {
    return {
      baseUrl: `${process.env.VUE_APP_RESOURCE_URL}`,
      name: ''
    }
  },
  mounted () {
    this.name = this.userInfo.name
  },
  computed: {
    ...mapGetters(['userInfo'])
  }
}
</script>

<style lang="scss" scoped="scoped">
@import "../../../assets/styles/mixin.scss";
.orderList{
	margin-bottom: 20px;
	.order-item{
		border-top: 1px solid #F1F4F4;
		padding-left: 16px;
		.orderId{
			line-height: 24px;
			padding: 12px 0;
			border-bottom: 1px solid #F1F4F4;
			.orderSn{
				font-size: 14px;
				color: #B6BBBB;
			}
			.orderStatus{
				font-size: 16px;
				color: #B6BBBB;
				margin-right: 16px;
			}
		}
		.order-content{
			position: relative;
			padding: 10px 0 20px;
			.content-text{
				margin-right: 136px;
				.title{
					font-size: 20px;
					line-height: 24px;
					color: $color-text;
					margin: 18px 0;
					font-weight: 600;
				}
				.msg{
					font-size: 14px;
					line-height: 18px;
					margin: 10px 0;
				}
			}
			.logo{
				position: absolute;
				top: 30px;
				right: 23px;
				width: 82px;
				height: 33px;
				overflow: hidden;
				img{
					float: right;
					height: 100%;
				}
			}
			.btn-contair{
				white-space: nowrap;
				position: absolute;
				bottom: 20px;
				right: 23px;
				height: 36px;
				>*{
					display: inline-block;
					border: 1px solid #1CD7D1;
					border-radius: 2px;
					height: 36px;
					padding: 0 17px;
					box-sizing: border-box;
					line-height: 34px;
					text-align: center;
					font-size: 14px;
				}
				>*{
					&:not(:first-child){
						margin-left: 12px;
					}
				}
				.toSettlement{
					color: $color-active-text;
				}
				.toDetail{
					background: #1FD8D2;
					color: #fff;
					cursor: pointer;
				}

			}
		}
	}
}
</style>
