<template>
	<div class="wrap">
		<div class="order-content">
			<TheCompiler :tabs='tabs' @changeTab='handleChangeTabs'></TheCompiler>
			<OrderList v-if='!noItem' :orderList='filtermessage'></OrderList>
			<null v-else></null>
		</div>
		<div class="ThePaginationBar" v-show="filtertotal">
			<ThePaginationBar :page='filterpage' :limit="limit" :total='filtertotal' @handleChange="handlePageChange"></ThePaginationBar>
		</div>
	</div>
</template>

<script>
import TheCompiler from '../TheCompiler.vue'
import ThePaginationBar from '@/components/ThePaginationBar'
import OrderList from '@/components/ThePersonCenter/GroupOrder/OrderList.vue'
import Null from '@/components/ThePersonCenter/NullFound/Null-Group.vue'
import {
  mapGetters,
  mapMutations
} from 'vuex'
export default {
  data () {
    return {
      tabs: [{
        tabtxt: '已出单',
        active: false,
        status: 2
      },
      {
        tabtxt: '保障中',
        active: true,
        status: 3
      },
      {
        tabtxt: '全部',
        active: false,
        status: ''
      }
      ],
      lists: {},
      page: {},
      total: {},
      limit: 5,
      noItem: false
    }
  },
  components: {
    TheCompiler,
    OrderList,
    Null,
    ThePaginationBar
  },
  methods: {
    handleChangeTabs (tabParam) {
      this.tabs.find(tab => tab.active).active = false
      tabParam.active = true
      this.getList()
    },
    handlePageChange () {
      this.$set(this.page, `G${this.status}`, val)
      this.getList()
    },
    getList () {
      this.noItem = false
      this.$api.personCenter.getGroupInsuranceList({
        page: this.filterpage,
        limit: this.limit,
        status: this.status
      }).then((data) => {
        this.renderList(data)
      })
    },
    renderList (data) {
      let list = []

      let total = 0
      if (data.code === 1) {
        try {
          list = data.data.data
        } catch (e) {
          console.error(e)
        };
        try {
          total = data.data.count
        } catch (e) {
          console.error(e)
        }
        this.$set(this.lists, `G${this.status}`, list)
        this.$set(this.total, `G${this.status}`, total)
        this.noItem = !list.length
        this.SET_GROUP_LIST_PAGE(JSON.stringify({
          page: this.filterpage,
          status: this.status
        }))
        console.log({
          page: this.filterpage,
          status: this.status
        })
      }
    },
    // 状态
    ...mapMutations(['SET_GROUP_LIST_PAGE'])
  },
  computed: {
    status () {
      try {
        return this.tabs.find(tab => tab.active).status
      } catch (e) {
        console.error(e)
        return 0
      }
    },
    filtermessage () {
      return this.lists[`G${this.status}`] || []
    },
    filtertotal () {
      return this.total[`G${this.status}`] || 0
    },
    filterpage () {
      return this.page[`G${this.status}`] || 1
    },
    // 状态
    ...mapGetters(['groupListPage'])
  },
  mounted () {
    let init = JSON.parse(this.groupListPage)
    console.log(this.groupListPage)
    if (init.page !== undefined && init.status !== undefined) {
      this.tabs.forEach(tab => {
        console.log('active', tab.status == init.status)
        tab.active = tab.status == init.status
      })
      this.$set(this.page, `G${this.status}`, init.page)
    }
    this.getList()
  }
}
</script>

<style scoped="scoped" lang="scss">
	.wrap {
		.order-content {
			background: #fff;
			box-shadow: 0 4px 12px 0 rgba(219, 219, 219, 0.25);
		}
		.ThePaginationBar {
			margin: 15px 0;
			text-align: right;
		}
	}
</style>
